const stopAndWait = (Vue, vm) =>{
	return new Promise((resolve, reject) =>{
		if(vm.$store.state.ifLogin === true) {
			resolve(true)
		}else {
			setTimeout(() =>{
				resolve(stopAndWait(Vue, vm))
			}, 200)
		}
	})
}

