import deepMerge from "../utils/deepMerge";
import validate from "../utils/test";
class Request {
	// 
	setConfig(customConfig) {
		// 
		this.config = deepMerge(this.config, customConfig);
	}

	// 
	async request(options = {}) {
		// 
		if (this.interceptor.request && typeof this.interceptor.request === 'function') {
			let tmpConfig = {};
			let requestState,interceptorRequest
			[requestState, interceptorRequest] = await this.interceptor.request(options);
			if (requestState === 0) {
				// pendingPromisepromisethen()
				this.options = interceptorRequest;
				
			}else if(requestState === 1) {
				return Promise.resolve({
					status: 1,
					message: ' ""'
				})
			}else if(requestState === 2) {}
		}
		options.dataType = options.dataType || this.config.dataType;
		options.responseType = options.responseType || this.config.responseType;
		options.url = options.url || '';
		options.params = options.params || {};
		options.header = Object.assign(this.config.header, options.header);
		options.method = options.method || this.config.method;

		return new Promise((resolve, reject) => {
			options.complete = (response) => {
				// loading(loading)
				if (this.config.showLoading) {
					uni.hideLoading();
					// loading
					clearTimeout(this.config.timer);
					this.config.timer = null;
				}
				// originalDatatrue(response)response.data
				if (this.config.originalData) {
					// 
					if (this.interceptor.response && typeof this.interceptor.response === 'function') {
						let resInterceptors = this.interceptor.response(response);
						// falsethis.$request.postthen
						if (resInterceptors !== false) {
							resolve(resInterceptors);
						} else {
							// falsecatch
							reject(response);
						}
					} else {
						// 
						resolve(response);
					}
				} else {
					if (response.statusCode == 200) {
						if (this.interceptor.response && typeof this.interceptor.response ===
							'function') {
							let resInterceptors = this.interceptor.response(response.data);
							if (resInterceptors !== false) {
								resolve(resInterceptors);
							} else {
								reject(response.data);
							}
						} else {
							// (originalData=false)then
							resolve(response.data);
						}
					} else {
						// 200modal
						// if(response.errMsg) {
						// 	uni.showModal({
						// 		title: response.errMsg
						// 	});
						// }
						reject(response)
					}
				}
			}

			// URL/,,/$requesttest.jsurl()
			options.url = validate.url(options.url) ? options.url : (this.config.baseUrl + (options.url
				.indexOf('/') == 0 ?
				options.url : '/' + options.url));

			// loading
			// timerid
			// loading
			if (this.config.showLoading && !this.config.timer) {
				this.config.timer = setTimeout(() => {
					uni.showLoading({
						title: this.config.loadingText,
						mask: this.config.loadingMask
					})
					this.config.timer = null;
				}, this.config.loadingTime);
			}

			uni.request(options);
		})
		// .catch(res => {
		// 	// reject()this.$request.post().then().catch()catct()
		// 	// catch()catch
		// 	return new Promise(()=>{});
		// })
	}

	constructor() {
		this.config = {
			baseUrl: 'http://47.102.157.175:28025', // 
			// 
			header: {},
			method: 'POST',
			// jsonuni.requestJSON.parse
			dataType: 'json',
			// 5+text
			responseType: 'text',
			showLoading: true, // loading
			loadingText: '...',
			loadingTime: 500, // ms
			timer: null, // 
			originalData: false, // 
			loadingMask: true, // loading
		}

		// 
		this.interceptor = {
			// 
			request: null,
			// 
			response: null
		}

		// get
		this.get = (url, data = {}, header = {}) => {
			return this.request({
				method: 'GET',
				url,
				header,
				data
			})
		}

		// post
		this.post = (url, data = {}, header = {}) => {
			return this.request({
				url,
				method: 'POST',
				header,
				data
			})
		}

		// put(HX2.6.15)
		this.put = (url, data = {}, header = {}) => {
			return this.request({
				url,
				method: 'PUT',
				header,
				data
			})
		}

		// delete(HX2.6.15)
		this.delete = (url, data = {}, header = {}) => {
			return this.request({
				url,
				method: 'DELETE',
				header,
				data
			})
		}
		
		//
		this.updateUserInfo = (userInfo, vm) =>{
			userInfo.pic = userInfo.avatarUrl
			vm.$store.commit('updateUserInfo', userInfo)
			this.put("/p/user/setUserInfo", userInfo).then(() =>{
				console.log(userInfo)
			})
		}
		

		this.login = () => {
			return (new Promise((resolve, reject) => {
				uni.login({
					success: res => {
						resolve(res)
					},
					failed: err => {
						reject(err)
					}
				})
			})).then(res => {
				let code = res.code
				return this.request({
					url: '/login?grant_type=mini_app',
					data: {
						principal: res.code,
						credentials: res.anonymousCode
					}
				}).then((res) => {
					console.log(res)
					if (res.userStatus == 0) {
						uni.showModal({
							showCancel: false,
							title: '',
							content: ''
						})
						uni.setStorageSync('token', res.access_token)
					} else {
						uni.setStorageSync('token', 'bearer' + res.access_token)
					}
					return res
				})
			})
		}











































	}
}
export default new Request
